<template>
  <div class="box">
    <van-index-bar highlight-color="#000" :sticky="false" :index-list="computedCityList" @select="handleSelect">
      <div v-for="data in cityList" :key="data.type">
        <van-index-anchor :index="data.type" />
        <van-cell :title="item.name" v-for="(item,index) in data.list" :key="index" @click="cityClick(item.name, item.cityId)" />
      </div>
    </van-index-bar>
  </div>
</template>

<script>
export default {
  name: 'CityList',
  props: ['cityList'],
  computed: {
    computedCityList() {
      return this.cityList.map(item => item.type)
    }
  },
  methods: {
    handleSelect(index) {
      console.log(this);
      // this.$toast(index)
    },
    cityClick(name, id) {
      this.$store.commit('city/changeCityName', name)
      this.$store.commit('city/changeCityId', id)
      // this.$store.commit('changeSelectCity', true) 
      this.$router.back()
      console.log(this.$store);
      console.log(name);
      // this.$router.go(0)
      // this.$router.back()
    }
  }
}
</script>

